// import ShowBox from "./components/ShowBox";
// import InputBox from "./components/InputBox"
import React from "react";
import Axios from "axios";
import { Input, Button, Card, Icon, Grid } from "semantic-ui-react";
import Keyboard from "react-simple-keyboard";
import "react-simple-keyboard/build/css/index.css";
import l from "../media/l.png"
import l1 from "../media/l1.png"
import l2 from "../media/l2.png"
import l3 from "../media/l3.png"
import l4 from "../media/l4.png"
import l5 from "../media/l5.png"
import r from "../media/r.png"
import r1 from "../media/r1.png"
import r2 from "../media/r2.png"
import r3 from "../media/r3.png"
import r4 from "../media/r4.png"
import r5 from "../media/r5.png"

class Joke extends React.Component {
  constructor() {
    super();
    this.state = {
      layoutName: "default",
      text: "",
      txtArr: [],
      inputTxt: "",
      inputArr: [],
      disabled: true,
      currentWord: "",
      currentWordPromp: "",
      nextFinger: "",
      lh: l,
      rh: r
    };
  }

  // handleText(): split and save state.text as state.txtArr
  handleText = () => {
    const slitArr = this.state.text.split(" ");
    this.setState({
      txtArr: slitArr,
      currentWord: slitArr[0],
    });
  };

  // getJoke(): is responsible of randomly pulls a joke one of the API, namely:
  //    1. https://v2.jokeapi.dev/joke/Any?type=twopart
  // and then save it to the state.text, and then saving it as an array to state.textArr
  getJoke = () => {
    // saving this to that
    const that = this;

    this.state.currentWordPrompIcon = <Icon loading name="spinner" />

    Axios.get("https://v2.jokeapi.dev/joke/Any?type=twopart").then((res) => {
      const setup = res.data.setup;
      const delivery = res.data.delivery;
      const sentence = setup + " " + delivery;

      // due to the joke api's messing up with "‘", "’", "“", "”",
      //    I need to manually replace them with the correct version of quotes.
      sentence
        .replace(/’/g, "'")
        .replace(/“/g, '"')
        .replace(/”/g, '"')
        .replace(/  +/g, " ");

      // saving to txtArr
      that.setState(() => ({
        text: sentence,
        currentWordPromp: "Next Word",
        currentWordPrompIcon: ""
      }));

      this.handleText();
      this.handleNextChar();
      this.getNextFingers();
    });
  };

  // match(): checks if an input word matches the txtArr
  //    check if a word is correct, incorrect, or not yet
  match = () => {
    const returnArr = [];
    for (var ii = 0; ii < this.state.txtArr.length; ++ii) {
      if (this.state.txtArr[ii] === this.state.inputArr[ii]) {
        // if it's correct, add class correct to the word
        // console.log("this.state.inputArr[ii + 1]: ", this.state.txtArr[ii + 1]);
        if (this.state.txtArr[ii + 1]) {
          this.state.currentWord = this.state.txtArr[ii + 1];
        }
        returnArr.push(
          <span className="correct">{this.state.txtArr[ii]} </span>
        );

        // if it's correct and it's the last word
        if (ii === this.state.txtArr.length - 1) {
          document.getElementById("getJokeButton").innerText = "Continue";
          if (this.state.text === this.state.inputTxt) {
            this.state.disabled = false;
            this.state.currentWord = "Congrads!";
            this.state.currentWordPromp = ""
            this.state.nextFinger = "r5"
            this.state.currentWordPrompIcon = <Icon name="smile" />
            console.log("all correct");
          }
        }
      } else if (!this.state.inputArr[ii]) {
        // if the user haven't yet reach the iith word
        returnArr.push(
          <span className="notExist">{this.state.txtArr[ii]} </span>
        );
      } else {
        // the next word, add class next
        if (ii === this.state.inputArr.length - 1) {
          returnArr.push(
            <span className="next">{this.state.txtArr[ii]} </span>
          );
        } else {
          // handle incorrect word input
          returnArr.push(
            <span className="incorrect">{this.state.txtArr[ii]} </span>
          );
        }
      }
    }
    return returnArr;
  };

  // generateAJoke(): generate a joke and clearing input
  generateAJoke = (ev) => {
    this.getJoke();

    // clearling input
    document.getElementById("typeInput").value = "";
    this.setState({
      inputTxt: "",
      inputArr: [],
      next: 0,
      disabled: true,
      currentWord: "",
      rh: r,
      lh: l,
    });
  };

  // handleInputChange(): handle changes to the input
  //    getting the value of input and saving to state
  handleInputChange = (ev) => {
    console.log(ev);

    this.setState(
      {
        inputTxt: ev.target.value,
      },
      () => {
        // allowing virtual keyboard input
        this.keyboard.setInput(ev.target.value);
      }
    );

    const splitArr = ev.target.value.split(" ");

    this.setState({
      inputArr: splitArr,
    });
  };

  // onChange(): handle virtual keyboard change (disabled)
  onChange = (input) => {
    // this.setState({
    //   inputTxt: input,
    // });
  };

  // onKeyPress(): handle virtual keyboard press (disabled)
  onKeyPress = (button) => {
    // if (button === "{shift}") {
    //   this.handleShift();
    // }
  };

  // handleShift(): change virtual keyboard
  handleShift = () => {
    let layoutName = this.state.layoutName;

    this.setState({
      layoutName: layoutName === "default" ? "shift" : "default",
    });
  };

  // isUpperCase(): checking if a key needs shift
  isUpperCase = (letter) => {
    // check special characters
    var upperArray = ["~", "!", "@", "#", "$", "%", "^", "&", "*", "(", ")", "_", "+", "{", "}", "|", ":", '"', "<", ">", "?",];
    for (var ii = 0; ii < upperArray.length; ii++) {
      if (upperArray[ii] === letter) {
        return true;
      }
    }

    // check numbers
    var lowerArray = ["`", "1", "2", "3", "4", "5", "6", "7", "8", "9", "0", "-", "=", "[", "]", "\\", ";", "'", ",", ".", "/",];
    for (var ii = 0; ii < lowerArray.length; ii++) {
      if (lowerArray[ii] === letter) {
        return false;
      }
    }

    // check letters
    try {
      return letter === letter.toUpperCase();
    } catch (e) {
      console.log(e);
    }
  };

  // toUnShift(): shift char to unshift char
  toUnShift = (letter) => {
    var upperArray = ["~", "!", "@", "#", "$", "%", "^", "&", "*", "(", ")", "_", "+", "{", "}", "|", ":", '"', "<", ">", "?",];
    var lowerArray = ["`", "1", "2", "3", "4", "5", "6", "7", "8", "9", "0", "-", "=", "[", "]", "\\", ";", "'", ",", ".", "/",];

    for (var ii = 0; ii < upperArray.length; ii++) {
      if (upperArray[ii] === letter) {
        return lowerArray[ii];
      }
    }

    try {
      return letter.toLowerCase();
    } catch (e) {
      console.log(e);
    }
  };

  // toShift(): unshift char to shift char
  toShift = (letter) => {
    var upperArray = ["~", "!", "@", "#", "$", "%", "^", "&", "*", "(", ")", "_", "+", "{", "}", "|", ":", '"', "<", ">", "?",];
    var lowerArray = ["`", "1", "2", "3", "4", "5", "6", "7", "8", "9", "0", "-", "=", "[", "]", "\\", ";", "'", ",", ".", "/",];

    for (var ii = 0; ii < lowerArray.length; ii++) {
      if (lowerArray[ii] === letter) {
        return upperArray[ii];
      }
    }

    try {
      return letter.toUpperCase();
    } catch (e) {
      console.log(e);
    }
  };

  getNextFingers = () => {
    if (this.state.inputTxt.length === this.state.text.length) {
      console.log("finished");
    } else {
      var nextKey = this.state.text.split("")[this.state.inputTxt.length];

      var fingerPicx = {
        l5: l5,
        l4: l4,
        l3: l3,
        l2: l2,
        l1: l1,
        r5: r5,
        r4: r4,
        r3: r3,
        r2: r2,
        r1: r1
      }

      var fingers = { 
        l5: ["`", "1", "q", "a", "z"],
        l4: ["2", "w", "s", "x"],
        l3: ["3", "e", "d", "c"],
        l2: ["4", "r", "f", "v", "g", "b", "t", "5"],
        r5: ["\\", "]", "[", "'", ";", "/", "p", "0", "-", "=", "Backspace", "Enter", "ShiftRight"],
        r4: ["(", "o", "l", "."],
        r3: ["*", "i", "k", ","],
        r2: ["7", "u", "j", "m", "6", "y", "h", "n"],
      }

      if (nextKey === " ") {
        // console.log("space")
        this.setState({
          nextFinger: "r1",
          rh: r1
        })
      }

      for (var key in fingers) {
        // console.log(fingers.key)
        for(var ii = 0; ii < fingers[key].length; ii++) {
          if (this.toUnShift(fingers[key][ii]) === this.toUnShift(nextKey)) {
            // console.log(key)
            // console.log(fingers[key][ii], nextKey)
            // console.log(this.toUnShift(fingers[key][ii]), this.toUnShift(nextKey))
            if (key[0] === "l") {
              this.setState({
                lh: fingerPicx[key],
                rh: r
              })
            } else if (key[0] === "r") {
              this.setState({
                rh: fingerPicx[key],
                lh: l
              })
            }

            this.setState({
              nextFinger: key
            })
          }
        }
      }
    }
  }

  // handleNextChar(): give users hints on what is the next char
  handleNextChar = () => {
    if (this.state.inputTxt.length === this.state.text.length) {
      console.log("finished");
    } else {
      var nextKey = this.state.text.split("")[this.state.inputTxt.length];
      console.log(nextKey);

      if (nextKey === " ") {
        nextKey = "{space}";
      }

      if (nextKey === "'" || nextKey === '"') {
        document
          .querySelector('[data-skbtn="' + "'" + '"]')
          .classList.add("buttonNext");
      } else if (nextKey === "\\") {
        document
          .querySelector('[data-skbtnuid="default-r1b13"]')
          .classList.add("buttonNext");
      } else if (this.isUpperCase(nextKey)) {
        try {
          document
            .querySelector('[data-skbtnuid="default-r3b0"]')
            .classList.add("buttonNext");
        } catch (e) {
          console.log(e);
        }
        try {
          document
            .querySelector("[data-skbtn='" + this.toUnShift(nextKey) + "']")
            .classList.add("buttonNext");
          document
            .querySelector("[data-skbtn='" + this.toShift(nextKey) + "']")
            .classList.add("buttonNext");
        } catch (e) {
          console.log(e);
        }
      } else {
        try {
          document
            .querySelector("[data-skbtn='" + this.toUnShift(nextKey) + "']")
            .classList.add("buttonNext");
          document
            .querySelector("[data-skbtn='" + this.toShift(nextKey) + "']")
            .classList.add("buttonNext");
        } catch (e) {
          console.log(e);
        }
      }
    }
  };

  // removeNext(): remove the previous hints
  removeNext = () => {
    try {
      var x = document.getElementsByClassName("buttonNext");
      console.log(x)
      for (var ii = 0; ii < x.length; ii++) {
        console.log(x[ii])
        x[ii].classList.remove("buttonNext")
      }
      document
            .querySelector('[data-skbtnuid="default-r3b0"]')
            .classList.remove("buttonNext");
    } catch(e) {
      console.log(e)
    }

    this.setState({
      lh: l,
      rh: r
    })
  }

  handleKeyPress = (ev) => {
    var key = ev.key;

    if (ev.code === "Enter") {
      return;
    }

    if (key === "Shift") {
      this.handleShift();
    }

    if (key === "CapsLock") {
      // this.handleShift();
    }

    if (key === " ") {
      key = "{space}";
    } else if (key === "Backspace") {
      key = "{bksp}";
      this.removeNext();
    } else if (key === "Tab") {
      key = "{tab}";
    }

    if (
      ev.code !== "ShiftLeft" &&
      ev.code !== "ShiftRight" &&
      ev.code !== "CapsLock" &&
      key !== "Meta" &&
      key.code !== "Delete"
    ) {
      // this.handleNextChar()
      if (key === "'") {
        document
          .querySelector('[data-skbtn="' + "'" + '"]')
          .classList.add("hg-activeButton");
      } else if (ev.code === "Backslash") {
        document
          .querySelector('[data-skbtnuid="default-r1b13"]')
          .classList.add("hg-activeButton");
      } else if (
        key === "Alt" ||
        key === "Control" ||
        key === "ArrowLeft" ||
        key === "ArrowRight" ||
        key === "ArrowUp" ||
        key === "ArrowDown"
      ) {
        console.log("alt || control");
      } else {
        document
          .querySelector("[data-skbtn='" + key + "']")
          .classList.add("hg-activeButton");
      }
    }
  };

  handleKeyUp = (ev) => {
    var key = ev.key;

    var caps_lock_on = ev.getModifierState("CapsLock");

    if (caps_lock_on == true) {
      alert("Caps Lock is turned on, please turn it off!");
      console.log("Caps Lock is turned on");
    }

    if (ev.code === "Enter") {
      if (!this.state.disabled) {
        this.generateAJoke();
      }
      return;
    }

    if (key === "Shift") {
      this.handleShift();
    }

    if (key === " ") {
      key = "{space}";
    } else if (key === "Backspace") {
      key = "{bksp}";
    } else if (key === "Tab") {
      key = "{tab}";
    }

    if (
      ev.code !== "ShiftLeft" &&
      ev.code !== "ShiftRight" &&
      ev.code !== "CapsLock" &&
      key !== "Meta" &&
      key.code !== "Delete"
    ) {
      if (key === "'") {
        document
          .querySelector('[data-skbtn="' + "'" + '"]')
          .classList.remove("hg-activeButton");
        document
          .querySelector('[data-skbtn="' + "'" + '"]')
          // .classList.remove("buttonNext");
      } else if (ev.code === "Backslash") {
        document
          .querySelector('[data-skbtnuid="default-r1b13"]')
          .classList.remove("hg-activeButton");
        document
          .querySelector('[data-skbtnuid="default-r1b13"]')
          // .classList.remove("buttonNext");
      } else if (
        key === "Alt" ||
        key === "Control" ||
        key === "ArrowLeft" ||
        key === "ArrowRight" ||
        key === "ArrowUp" ||
        key === "ArrowDown"
      ) {
        console.log("alt || control");
      } else {
        document
          .querySelector("[data-skbtn='" + key + "']")
          .classList.remove("hg-activeButton");
        document
          .querySelector("[data-skbtn='" + key + "']")
          // .classList.remove("buttonNext");
      }
    }

    this.removeNext();
    this.handleNextChar();
    this.getNextFingers()
  };

  componentDidMount = () => {
    this.getJoke();
  };

  render() {
    return (
      <div>
        <div>
          <div id="mainText">
            <p>{this.match()}</p>
          </div>

          <h1>finger: {this.state.nextFinger}</h1>

          <Grid celled='internally'>
            <Grid.Row>
              <Grid.Column width={5} className="handBox">
                <img src={this.state.lh} class="hand" />
              </Grid.Column>
              <Grid.Column width={6}>
                <Card>
                  <Card.Content id="currentWordPromp">
                    <Card.Description>
                      {this.state.currentWordPromp}
                      {this.state.currentWordPrompIcon}
                    </Card.Description>
                  </Card.Content>
                  <Card.Content>
                    <Card.Header id="currentWordDis" className="next">
                      {this.state.currentWord}
                    </Card.Header>
                  </Card.Content>
                </Card>

                <div id="inputAndButton">
                  <Input
                    placeholder="Type here"
                    autoComplete="off"
                    onChange={this.handleInputChange}
                    id="typeInput"
                    value={this.state.inputTxt}
                    onKeyDown={this.handleKeyPress}
                    onKeyUp={this.handleKeyUp}
                    maxLength={this.state.text.length}
                  />
                  <Button
                    onClick={this.generateAJoke}
                    id="getJokeButton"
                    disabled={this.state.disabled}
                    color="green"
                  >
                    Get a joke
                  </Button>
                </div>
              </Grid.Column>
              <Grid.Column width={5} className="handBox">
                <img src={this.state.rh} class="hand" />
              </Grid.Column>
            </Grid.Row>
          </Grid>

          

          <div id="virtualKeyBoard">
            <Keyboard
              keyboardRef={(r) => (this.keyboard = r)}
              theme={"hg-theme-default"}
              layoutName={this.state.layoutName}
              onChange={(input) => this.onChange(input)}
              onKeyPress={(button) => this.onKeyPress(button)}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default Joke;
